import cookie from 'react-cookies'

// cookie
export const loginUser = () => {
  return cookie.load('userInfo')
}

// cookie
export const onLogin = (user) => {
  cookie.save('userInfo', user, { path: '/' })
}

// cookie
export const logout = () => {
  cookie.remove('userInfo')
  window.location.href = '/Login'
}
